import { useRouter } from 'next/router'
import theme from '../theme'
import { useState, useEffect } from 'react'
import ConfirmModal from './ConfirmModal'
import { FaSignOutAlt } from 'react-icons/fa'

export default function Layout({ user, title, children }) {
  const router = useRouter()
  const [showLogoutModal, setShowLogoutModal] = useState(false)

  const role = user?.role || "pharmacist"

  // ÿ±Ÿàÿßÿ®ÿ∑ ÿ´ÿßÿ®ÿ™ÿ© (Frontend ŸÅŸÇÿ∑)
  const navConfig = {
    admin: [
      { name: 'üè† ŸÑŸàÿ≠ÿ© ÿßŸÑÿ™ÿ≠ŸÉŸÖ', path: '/dashboard' },
      { name: 'üíä ÿßŸÑÿ£ÿØŸàŸäÿ©', path: '/pharmacist' },
      { name: 'üßæ ŸÜŸÇÿ∑ÿ© ÿßŸÑÿ®Ÿäÿπ', path: '/cashier' },
      { name: 'üì¶ ÿßŸÑŸÖÿÆÿ≤ŸÜ', path: '/inventory' },
      { name: 'üìä ÿßŸÑÿ™ŸÇÿßÿ±Ÿäÿ±', path: '/reports' },
      { name: 'üë• ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸàŸÜ', path: '/users' },
    ],
    pharmacist: [
      { name: 'üíä ÿßŸÑÿ£ÿØŸàŸäÿ©', path: '/pharmacist' },
      { name: 'üì¶ ÿßŸÑŸÖÿÆÿ≤ŸàŸÜ', path: '/inventory' },
      { name: 'üìä ÿ™ŸÇÿßÿ±Ÿäÿ± ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™', path: '/reports' },
    ],
    cashier: [
      { name: 'üßæ ŸÜŸÇÿ∑ÿ© ÿßŸÑÿ®Ÿäÿπ', path: '/cashier' },
      { name: 'üìÑ ÿßŸÑÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸäŸàŸÖŸä', path: '/shift' },
    ],
  }

  const links = navConfig[role] || []

  const handleLogout = () => {
    localStorage.removeItem("pharmacy_user")
    router.replace("/")
  }

  return (
    <div dir="rtl" className="flex flex-col min-h-screen bg-gray-50">
      <header
        className="sticky top-0 z-40 w-full bg-white border-b shadow-sm"
        style={{ borderColor: `${theme.colors.primary}20` }}
      >
        <div className="flex flex-col items-center justify-between gap-3 px-4 py-3 mx-auto sm:flex-row max-w-7xl">
          <div className="flex items-center gap-2">
            <div
              className="flex items-center justify-center w-10 h-10 text-xl font-bold text-white rounded-md shadow"
              style={{ background: theme.colors.primary }}
            >
              üíä
            </div>
            <div>
              <h1 className="text-lg font-bold text-gray-800">ŸÜÿ∏ÿßŸÖ ÿßŸÑÿµŸäÿØŸÑŸäÿ© ÿßŸÑÿ∞ŸÉŸä</h1>
              <p className="text-xs text-gray-500 -mt-0.5">Pharmacy Management System</p>
            </div>
          </div>

          <nav className="flex flex-wrap justify-center gap-1 sm:gap-2">
            {links.map((item) => (
              <button
                key={item.path}
                onClick={() => router.push(item.path)}
                className={`px-3 py-1.5 text-sm font-medium rounded-md border transition-all ${
                  router.pathname === item.path
                    ? 'text-white shadow-sm'
                    : 'text-gray-700 hover:text-sky-700 hover:bg-sky-50'
                }`}
                style={{
                  backgroundColor:
                    router.pathname === item.path
                      ? theme.colors.primary
                      : 'transparent',
                  borderColor:
                    router.pathname === item.path
                      ? theme.colors.primary
                      : '#e5e7eb',
                }}
              >
                {item.name}
              </button>
            ))}
          </nav>

          <div className="flex items-center gap-3">
            <div className="text-sm text-gray-700">
              ŸÖÿ±ÿ≠ÿ®Ÿãÿßÿå{' '}
              <span className="font-semibold text-sky-700">
                {user?.name || 'ŸÖÿ≥ÿ™ÿÆÿØŸÖ'}
              </span>
              <span className="ml-1 text-gray-500">
                ({role === 'admin' ? 'ŸÖÿØŸäÿ±' : role === 'cashier' ? 'ŸÉÿßÿ¥Ÿäÿ±' : 'ÿµŸäÿØŸÑŸä'})
              </span>
            </div>

            <button
              onClick={() => setShowLogoutModal(true)}
              className="flex items-center gap-2 px-4 py-2 text-sm font-medium text-white transition-all duration-200 border rounded-md shadow-sm"
              style={{
                backgroundColor: 'rgba(239, 68, 68, 0.85)',
                borderColor: 'rgba(239, 68, 68, 0.5)',
              }}
            >
              <FaSignOutAlt className="text-lg" />
              <span>ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨</span>
            </button>
          </div>
        </div>
      </header>

      <main className="flex-1 w-full px-4 py-6 mx-auto max-w-7xl sm:px-6 lg:px-8">
        {title && (
          <h2 className="pb-2 mb-6 text-2xl font-bold text-gray-800 border-b border-gray-200">
            {title}
          </h2>
        )}
        {children}
      </main>

      <footer className="py-3 mt-auto text-xs text-center text-gray-400 border-t border-gray-100">
        ¬© {new Date().getFullYear()} ŸÜÿ∏ÿßŸÖ ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿµŸäÿØŸÑŸäÿ© ‚Äî ÿ¨ŸÖŸäÿπ ÿßŸÑÿ≠ŸÇŸàŸÇ ŸÖÿ≠ŸÅŸàÿ∏ÿ©
      </footer>

      <ConfirmModal
        visible={showLogoutModal}
        title="ÿ™ÿ£ŸÉŸäÿØ ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨"
        message="ŸáŸÑ ÿ™ÿ±ÿ∫ÿ® ŸÅŸä ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨ ŸÖŸÜ ÿßŸÑŸÜÿ∏ÿßŸÖÿü"
        confirmText="ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨"
        confirmColor={theme.colors.danger}
        onConfirm={handleLogout}
        onCancel={() => setShowLogoutModal(false)}
      />
    </div>
  )
}






















