import { useState } from "react";
import Layout from "../components/Layout";
import Modal from "../components/Modal";
import { mockUsers } from "../mock/data";

export default function UsersPage() {
  const [user] = useState({ name: "ÿßŸÑŸÖÿØŸäÿ± ÿ£ÿ≠ŸÖÿØ", role: "admin" });
  const [users, setUsers] = useState(mockUsers);
  const [modalOpen, setModalOpen] = useState(false);

  const [form, setForm] = useState({
    name: "",
    username: "",
    role: "pharmacist",
  });

  const openModal = () => {
    setForm({ name: "", username: "", role: "pharmacist" });
    setModalOpen(true);
  };

  const saveUser = () => {
    const newUser = {
      id: users.length + 1,
      ...form,
    };

    setUsers([...users, newUser]);
    setModalOpen(false);
  };

  const deleteUser = (id) => {
    setUsers(users.filter((u) => u.id !== id));
  };

  return (
    <Layout user={user} title="üë• ÿ•ÿØÿßÿ±ÿ© ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ">
      <div dir="rtl" className="space-y-6">

        <div className="flex justify-end mb-4">
          <button
            onClick={openModal}
            className="px-4 py-2 text-white rounded-md bg-sky-600 hover:bg-sky-700"
          >
            ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ
          </button>
        </div>

        <div className="overflow-x-auto bg-white border rounded-lg shadow-sm">
          <table className="w-full min-w-[800px] text-sm text-right">
            <thead className="text-gray-600 bg-gray-50">
              <tr>
                <th className="px-3 py-2">ÿßŸÑÿßÿ≥ŸÖ</th>
                <th className="px-3 py-2">ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ</th>
                <th className="px-3 py-2">ÿßŸÑÿØŸàÿ±</th>
                <th className="px-3 py-2">ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
              </tr>
            </thead>
            <tbody>
              {users.map((u) => (
                <tr key={u.id} className="border-t hover:bg-gray-50">
                  <td className="px-3 py-2 font-semibold">{u.name}</td>
                  <td className="px-3 py-2">{u.username}</td>
                  <td className="px-3 py-2">
                    {u.role === "admin"
                      ? "ŸÖÿØŸäÿ±"
                      : u.role === "pharmacist"
                      ? "ÿµŸäÿØŸÑŸä"
                      : "ŸÉÿßÿ¥Ÿäÿ±"}
                  </td>
                  <td className="px-3 py-2">
                    <button
                      onClick={() => deleteUser(u.id)}
                      className="px-3 py-1 text-sm text-red-600 rounded-md hover:bg-red-50"
                    >
                      ÿ≠ÿ∞ŸÅ
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* ŸÜÿßŸÅÿ∞ÿ© ÿßŸÑÿ•ÿ∂ÿßŸÅÿ© */}
        {modalOpen && (
          <Modal
            title="ÿ•ÿ∂ÿßŸÅÿ© ŸÖÿ≥ÿ™ÿÆÿØŸÖ"
            onClose={() => setModalOpen(false)}
            onConfirm={saveUser}
            confirmText="ÿ≠ŸÅÿ∏"
          >
            <div className="space-y-4">
              <input
                type="text"
                placeholder="ÿßŸÑÿßÿ≥ŸÖ"
                value={form.name}
                onChange={(e) => setForm({ ...form, name: e.target.value })}
                className="w-full px-3 py-2 border rounded-md"
              />

              <input
                type="text"
                placeholder="ÿßÿ≥ŸÖ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ"
                value={form.username}
                onChange={(e) => setForm({ ...form, username: e.target.value })}
                className="w-full px-3 py-2 border rounded-md"
              />

              <select
                value={form.role}
                onChange={(e) => setForm({ ...form, role: e.target.value })}
                className="w-full px-3 py-2 border rounded-md"
              >
                <option value="admin">ŸÖÿØŸäÿ±</option>
                <option value="pharmacist">ÿµŸäÿØŸÑŸä</option>
                <option value="cashier">ŸÉÿßÿ¥Ÿäÿ±</option>
              </select>
            </div>
          </Modal>
        )}
      </div>
    </Layout>
  );
}


















